import React from 'react';

class FoodList extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        error: null,
        isLoaded: false,
        foodList: []
      };
    }
  
    async componentDidMount() {
        try {
            const data = await fetch("/api");
            console.log('request complete');
            console.log(data);
            const value = await data.json();
            console.table(value);
            this.setState({
                isLoaded: true,
                foodList: value
            });
        } catch(err) {
            console.error(err);
                this.setState({
                  isLoaded: true,
                  err
                });
        }
    }

    render() {
        const { error, isLoaded, foodList } = this.state;


        if (error) {
        return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
        return <div>Loading...</div>;
        } else {
        return (
            <ul>
            {foodList.map(item => (
                <li key={item.id}>
                {item.name} {item.price}
                </li>
            ))}
            </ul>
        );
        }
      }
    }
  
  export default FoodList;